import React, { Component } from 'react';
import './Projects.css';
import Book from "../images/Book.PNG"
import Food from "../images/Food.PNG";
import CurrencyApp from "../images/cc1.PNG";
import FT from "../images/FT.PNG";

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }

    handleClick = (link) =>{
        console.log("clicked" + link);
        window.open(link, "_blank");
    }

    render() { 
        return ( 
            <div id="mainProjectContainer">
                <nav>
                    <ul id="nav-links">
                        <li>
                            <a href="#BookDatabase">Book Database</a>
                        </li>
                        <li>
                            <a href="#FoodGuide">Food Recipe</a>  
                        </li>
                        <li>
                            <a href="#CurrencyExchange">Currency Exchange</a>
                        </li>
                        <li>
                            <a href="#FloralTrauma">Floral Trauma</a>
                        </li>
                    </ul>
                </nav>
                <div> 
                    <div id="pText">
                        <h1 id="BookDatabase">Book Database Website</h1>

                        <div>Using Google Books API you can search for a book franchise you enjoy and get 
                        information about the book name, book genre and book description in this website.</div>
                    </div>  
                    <img src={Book} onClick={() => this.handleClick("https://github.com/MaxBulatov/BooksApp")}></img>
                </div>

                <div>
                    <div id="pText">
                        <h1 id="FoodGuide">Food Recipe Guide</h1>

                        <div> Using Edamamne Food API you can search for food recipe's with a specific theme, like
                        chicken, beef or noodles for example.</div>
                    </div>  
                    <img src={Food} onClick={() => this.handleClick("https://food-guide-api.herokuapp.com/")}></img>
                </div>

                <div>
                    <div id="pText">
                        <h1 id="CurrencyExchange">Currency Exchange App</h1>

                        <div>Using Exchange Rate API you can see the currency ratio between counties.</div>
                    </div>  
                    <img src={CurrencyApp} onClick={() => this.handleClick("https://github.com/MaxBulatov/CurrencyExchange")}></img>
                </div>

                <div>
                    <div id="pText">
                        <h1 id="FloralTrauma">Floral Trauma Multiplayer Game</h1>

                        <div>Our graduating class of 24 students worked on this game thought out our 4th semester at BCIT. 
                            My personal contribution to this project was testing, quality assurance and making sure the coding industry standard was followed.
                            This was acheived through conducting usability studies on game design descisions as well as conducting function and user testing
                            to make sure the game ran smoothly.
                        </div>
                    </div>  
                    <img src={FT} onClick={() => this.handleClick("https://floral-trauma-mmst.herokuapp.com/")}></img>
                </div>
                
            </div> 
        );
    }
}
 
export default Projects;